:root {
    --banner-height: 4rem;
    --header-height: 6rem;
    --footer-height: 8rem;

    --100vh: 100vh;

    --font-title: 'Barlow', sans-serif;
    --font-text: 'Roboto Flex', sans-serif;
}

:root,
body .white {
    --box-shadow-1: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.25);
    --box-shadow-2: 0.1rem 0.2rem 2rem 0.1rem rgba(0, 0, 0, 0.1);
    --box-shadow-3: 0.4rem 0.4rem 1rem 0.5rem rgba(0, 0, 0, 0.25);
    --box-shadow-4: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);

    /*outline*/
    --box-shadow-hover-1: 0 0 0 0.5rem var(--secondary-1-active);
    --box-shadow-hover-2: 0 0 0 1.5rem var(--secondary-1-active);
    --outline: 0 0 0 0.2rem var(--primary-1), 0 0 0 0.3rem var(--white), 0 0 0 0.6rem var(--secondary-1);
    --outline-2: 0 0 0 0.2rem var(--primary-1), 0 0 0 0.4rem var(--white), 0 0 0 0.9rem var(--secondary-1);
}

.black {
    --box-shadow-1: 0 0.1rem 0.2rem rgba(255, 255, 255, 0.25);
    --box-shadow-2: 0.1rem 0.2rem 2rem 0.1rem rgba(255, 255, 255, 0.1);
    --box-shadow-3: 0.4rem 0.4rem 1rem 0.5rem rgba(255, 255, 255, 0.25);
    --box-shadow-4: 0 0.4rem 0.4rem rgba(255, 255, 255, 0.25);

    /*outline*/
    --box-shadow-hover-1: 0 0 0 0.5rem var(--secondary-1-active);
    --box-shadow-hover-2: 0 0 0 1.5rem var(--secondary-1-active);
    --outline: 0 0 0 0.2rem var(--primary-1), 0 0 0 0.3rem var(--white), 0 0 0 0.6rem var(--secondary-1);
    --outline-2: 0 0 0 0.2rem var(--primary-1), 0 0 0 0.4rem var(--white), 0 0 0 0.9rem var(--secondary-1);
}
