:root,
body .white {
    --primary-1: #0565f6;
    --primary-1-hover: #0350c2;
    --primary-1-active: #063d91;

    --primary-2: #7a45f8;
    --primary-2-hover: #6026e8;
    --primary-2-active: #4f07c5;

    --secondary-1: rgb(235, 242, 250);
    --secondary-1-hover: rgb(219, 232, 255);
    --secondary-1-active: rgb(200, 216, 255);

    --secondary-2: rgb(226, 223, 234);
    --secondary-2-hover: rgb(214, 208, 230);
    --secondary-2-active: rgb(208, 200, 228);

    --success: #158c38;
    --success-hover: #0c6b27;
    --success-active: #1f6b0a;

    --success-2: #d6efdc;
    --success-2-hover: #c1dec8;
    --success-2-active: #aaceb3;

    --warning: #f39404;
    --warning-hover: #bd7406;
    --warning-active: #915a08;

    --red: #ff2424;
    --red-hover: #c23e3e;
    --red-active: #8f2b2b;

    --white: rgb(255, 255, 255);
    --true-white: rgb(255, 255, 255);
    --white-01: rgb(255, 255, 255, 0.1);
    --white-02: rgb(255, 255, 255, 0.2);
    --white-03: rgb(255, 255, 255, 0.3);
    --white-04: rgb(255, 255, 255, 0.4);
    --white-05: rgb(255, 255, 255, 0.5);

    --black: rgb(0, 0, 0);
    --true-black: rgb(0, 0, 0);
    --black-01: rgb(0, 0, 0, 0.1);
    --black-02: rgb(0, 0, 0, 0.2);
    --black-03: rgb(0, 0, 0, 0.3);
    --black-04: rgb(0, 0, 0, 0.4);
    --black-05: rgb(0, 0, 0, 0.5);

    --gray-1: #fafafa;
    --gray-2: #f5f5f5;
    --gray-3: #f0f0f0;
    --gray-4: #d9d9d9;
    --gray-5: #bfbfbf;
    --gray-6: #8c8c8c;
    --gray-7: #595959;
    --gray-8: #434343;
    --gray-9: #262626;
    --gray-10: #141414;
}
